$normal-text-color: #111;
$nav-link-color: #333;
$nav-link-hover-color: black;
$link-color: #444;
$last-p-padd: 1.5em;
$nav-padd: 1em;
$line-under: 1px solid #aaa; 

body {
  background-color: #fefefe;
  padding: 15px;
  margin: auto;
  max-width: 600px;
  font-family: -apple-system, helvetica, arial, sans-serif;
}
#wrapper {
  color: $normal-text-color;
  font-size: 14px;
}
#main-name {
  color: rgba(0, 0, 0, 0.8);
}
h1, h2, h3, h4, h5, h6 {
}
h1 {
  font-size: 1.5em;
}
h2 {
  font-size: 1.3em;
}
h4 {
  font-weight: normal;
}
a {
  text-decoration: none;
  color: $link-color;
}
label {
  font-size: .8em;
}

#menu,
label[for="menu"]{
  display: none;
}

nav {
  padding: $nav-padd 0px;
  margin: 0px;
}
nav a:first-of-type {
  margin-left: 0;
}
nav a {
  margin: 1em;
  color: $nav-link-color;
  font-weight: bold;
  font-style: none;
}
nav a.on-page {
  color: #888;
}
nav a:hover {
  text-decoration: underline; 
}

li {
  margin: .5em;
}

#main-img {
  width: 100%;
}
p {
  padding: .5em 0;
  line-height: 1.4em;
}
.line-under {
  padding-bottom: $last-p-padd;
  border-bottom: $line-under;
}
.article {
}
.article a {
  text-decoration: underline;
}
.article a:hover {
  color: rgba(0, 0, 0, 0.5);
}
.post-date {
  text-transform: uppercase;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.5);
}

.post-desc {
  color: rgba(0, 0, 0, .7);
  padding: 10px;
  font-weight: lighter;
}

p.post-excerpt {
  margin-top: 0;
  padding-top: 10px;
}


table,
table tr,
table td,
table th{
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-collapse: collapse;
  padding: 5px;
  font-weight: normal;
}
table {
  width: 75%;
  margin: auto;
}

table.post-list,
table.post-list tr,
table.post-list td {
  width: 100%;
  border: none;
  padding-left: 0;
}
img {
  display: block;
  width: 55%;
  margin-left: auto;
  margin-right: auto;
}

blockquote {
  font-style: italic;
}

@media screen and (max-width: 600px){
  #menu,
  label[for="menu"]{
    display: inline-block;
    font-size: 20px;
  }
  body {
    width: 90%;
  }
  #info {
    margin: 0 7px;
  }
  .menu-content {
    max-height: 0;
    overflow: hidden;
  }
  nav {
    width: 100%;
  }
  nav a {
    display: block;
    padding-left: 0;
    margin-left: 0;
  }
  input#menu{
    display: none;
  }
  input:checked ~ .menu-content {
    max-height: 100%;
    border-bottom: $line-under;
    color: red;
  }
}

.contact-info {
  width: 80%;
  word-wrap: break-word;
}
.contact-info p {
  margin: 0;
  padding: 0;
}

.mono {
  font-family: monospace;
}
